import React, {Component} from 'react';
import {CloseButton, Offcanvas} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

class SidePanel extends Component {
    state = {
        sections: [
            {
                title: "Sell & Buy",
                tabs: [
                    {
                        name: "Sell Now"
                    },
                    {
                        name: "But Now"
                    }
                ]
            },
            {
                title: "Support and Services",
                tabs: [
                    {
                        name: "Customer Service"
                    },
                    {
                        name: "Repair my bicycle"
                    },
                    {
                        name: "Extensions for bicycle"
                    }
                ]
            },
            {
                title: "My options",
                tabs: [
                    {
                        name: "Cart"
                    },
                    {
                        name: "My Account"
                    }
                ]
            }
        ]
    }

    render() {
        return (
            <div>
                <Offcanvas show={this.props.show} onHide={this.props.onHide} style={{width: 280}}>
                    <Offcanvas.Header className={"bg-primary text-white px-2"}>
                        <Offcanvas.Title>
                            <FontAwesomeIcon icon={"user-circle"} className={"me-2"} size={"1x"} />
                            Login
                        </Offcanvas.Title>
                        <CloseButton variant={"white"} className={"shadow-none"} onClick={this.props.onHide} />
                    </Offcanvas.Header>
                    <Offcanvas.Body className={"border-top p-0"}>
                        {
                            this.state.sections.map((section, s_index) => {
                                return <div key={"s_section_" + s_index} className={"border-top p-2"}>
                                    <div>
                                        <h6 className={"fw-bold py-1"}>{section.title}</h6>
                                    </div>
                                    {
                                        section.tabs.map((tab, t_index) => {
                                            return <div key={"s_tab_" + s_index + "_" + t_index}>
                                                <p className={""}>{tab.name}</p>
                                            </div>;
                                        })
                                    }
                                </div>
                            })
                        }
                    </Offcanvas.Body>
                </Offcanvas>
            </div>
        );
    }
}

export default SidePanel;