import React, {Component} from 'react';
import LoginPage from "../../login/LoginPage";
import {Col, Container, Row} from "react-bootstrap";

class Account extends Component {
    state = {
        login: true,
        sections: [
            {
                title: "My order",
                subtitle: "Your bicycle orders, sells, track, return"
            },
            {
                title: "Login and Security",
                subtitle: "Manage account, password, 2fa"
            },
            {
                title: "My address",
                subtitle: "edit, delete, add address"
            }
        ]
    }
    render() {
        return (
            <section>
                {
                    this.state.login ?
                        <Container>
                            <div className={"p-2"}>
                                <div className={"p-2"}>
                                    <h4>My Account</h4>
                                </div>

                                <Row>
                                    {
                                        this.state.sections.map((section, index) => {
                                            return <Col key={"account_" + index} md={4} sm={6} xs={12}>
                                                <div className={"p-4 rounded bg-white shadow-sm m-2"}>
                                                    <h6>{section.title}</h6>
                                                    <p className={"text-secondary"}>
                                                        {
                                                            section.subtitle
                                                        }
                                                    </p>
                                                </div>
                                            </Col>
                                        })
                                    }
                                </Row>
                            </div>
                        </Container>:
                        <div>
                            <div className={"text-center p-2 bg-light-green"}>
                                Login to access your account
                            </div>
                            <div className={"mt-4"}>
                                <LoginPage />
                            </div>
                        </div>
                }
            </section>
        );
    }
}

export default Account;